import React, { Component } from 'react'
import { primeraMayuscula } from '../../helpers';


class Resume extends Component {

   showResume = () => {

      const {brand, year, plan} = this.props.infoAuto;

      if(!brand || !year || !plan) return null;

      return (

         <div className="uk-card uk-card-secondary uk-padding-small uk-margin-small">
            <h5 className="uk-text-uppercase uk-text-center">Resumen cotización</h5>
            <table className="uk-table uk-table-small">
               <thead>
                  <tr>
                     <th>Marca</th>
                     <th>Año</th>
                     <th>Plan</th>
                  </tr>
               </thead>
               <tbody>
                  <tr>
                     <td>{ primeraMayuscula(brand) }</td>
                     <td>{ primeraMayuscula(year) }</td>
                     <td>{ primeraMayuscula(plan) }</td>
                  </tr>
               </tbody>
            </table>
         </div>
      );
      
   }

   render() {

      return(
         <div>
            { this.showResume() }
         </div>

      );

   }
}

export default Resume;