import React, { Component } from 'react'
import Formulario from './modules/FormCotizador';
import '../css/modules/cotizador.css';
import Resume from './modules/Resume';
import Result from './modules/Result';
import { obtenerDiferenciaAnio, calcularMarca, obtenerPlan } from '../helpers';


class Cotizador extends Component{

   state = {
      result : '',
      information : {}
   }

   quoteSafe = (data) => {

      const {brand, year, plan} = data;

      // Precio base del seguro de 500.000
      let result = 500000;      

      // Obtener diferencia de años      
      const difference = obtenerDiferenciaAnio(year);

      // Por cada año restar el 3% del valor base del seguro
      result -= ((difference * 3) * result) / 100;

      // Aumenta costo base del seguro según el tipo de marca del vehiculo
      result = calcularMarca(brand) * result;

      // Plan Básico aumenta el valor del seguro un 20% y el completo un 50%
      let increment = obtenerPlan(plan);

      result = parseFloat(increment * result).toFixed(2);

      // Mostrar Resumen del seguro
      const infoAuto = {
         brand  : brand,
         year   : year,
         plan   : plan
      }

      // Actualiza el State
      this.setState({
         result: result,
         information : infoAuto
      })

   }

   render(){

      return(
   
         <div className="uk-container-small container">
   
            <div className="form-section">
               <Formulario 
                  quoteSafe={this.quoteSafe} 
               />
            </div>
   
            <div className="resume-section">
               <Resume 
                  infoAuto={this.state.information} 
               />
            </div>

            <div className="result-section">
               <Result
                  result={this.state.result} 
                />
            </div>
   
         </div>
   
   
      );
   }


}

export default Cotizador;