import React, { Component } from 'react';
import { TransitionGroup, CSSTransition } from 'react-transition-group';


class Result extends Component {

   render() {

      const result = this.props.result;

      let Message = (!result) ? `Seleccione una marca, año y tipo de seguro` : `El total es: $ ${result}`;

      return (

         <div className="uk-card uk-card-primary uk-padding-small">
            <h5 className="uk-text-uppercase uk-text-center animation" key={result}>{Message}</h5>
         </div>
         
      );



   }

   
   
}


export default Result;