import React from 'react';

class FormCotizador extends React.Component{


   brandRef = React.createRef();
   yearRef  = React.createRef();
   planRef  = React.createRef();

   
   quoteSafe = (e) => {

      e.preventDefault(); 
      
      // Obtener Datos
      const infoAuto = {
         'brand'  : this.brandRef.current.value,
         'year'   : this.yearRef.current.value,
         'plan'   : this.planRef.current.value
      }

      this.props.quoteSafe(infoAuto);

      // Resetea el Formulario
      // e.currentTarget.reset();
      
   }

   render(){

      return (

         <div className="uk-text-center">

            <h1>Cotizador Seguro de Auto</h1>

            <form action="#" className="cotizar-auto" onSubmit={this.quoteSafe}>

               <div className="uk-child-width-1-3@m uk-grid uk-grid-small">
                  <div className="uk-form-controls">
                     <label>Marca</label>
                     <select className="uk-select" name="brand" ref={this.brandRef}>
                        <option value="">Seleccione</option>
                        <option value="americano">Americano</option>
                        <option value="europeo">Europeo</option>
                        <option value="asiatico">Asiatico</option>
                     </select>
                  </div>

                  <div className="uk-form-controls">
                     <label>Año</label>
                     <select className="uk-select" name="year" ref={this.yearRef}>
                        <option value="">Seleccione</option>
                        <option value="2018">2018</option>
                        <option value="2017">2017</option>
                        <option value="2016">2016</option>
                        <option value="2015">2015</option>
                        <option value="2014">2014</option>
                        <option value="2013">2013</option>
                        <option value="2012">2012</option>
                        <option value="2011">2011</option>
                        <option value="2010">2010</option>
                        <option value="2009">2009</option>
                        <option value="2008">2008</option>
                     </select>
                  </div>

                  <div className="uk-form-controls">
                     <label>Plan</label>
                     <select className="uk-select" name="plan" ref={this.planRef}>
                        <option value="">Seleccione</option>
                        <option value="Basico">Básico</option>
                        <option value="Completo">Completo</option>
                     </select>
                  </div>
                  
               </div>

               <button className="uk-button uk-button-primary uk-margin uk-width-1-1@m">Cotizar</button>

            </form>

         </div>
      );

   }

}


export default FormCotizador

